
@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


@keyframes fadeInTranslate2 {
    0% {
        transform: translateY(100px);
        opacity: 0;
    }

    65% {
        transform: translateY(50px);
        opacity: 0;
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}



@keyframes fadeInTranslate1 {
    0% {
        transform: translateY(100px);
        opacity: 0;
    }

    60% {
        transform: translateY(50px);
        opacity: 0;
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: scale(3);
    }

    20% {
        opacity: 0;
        transform: scale(3);
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

